.App{
  padding: 0;
  margin: 0;
  ::placeholder{
    color: #ffffff;
    opacity: 0.8;
  }
  .welcome{
    background-color: #1A417c;
    color: #ffffff;
    text-align: center;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    .welcome-container{
      width: 50%;
      .enter{
        margin: 0 auto;
        width: 50%;
        display: flex;
        justify-content: space-evenly;
        button{
          padding: 5px 10px;
          &:hover{
            background-color: #1A417c;
            color: #ffffff;
            border: 1px solid #ffffff;
          }
        }
      }

    }
    
  }


  .login{
    background-color: #1A417c;
    color: #ffffff;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    .login-container{
      width: 50%;
      text-align: center;
      input{
        display: block;
        padding: 7px 10px;
        width: 70%;
        background-color: #1A417c;
        color: #ffffff;
        border: 1px solid #ffffff;
        margin: 1em auto;
        margin-bottom: 0;
       
      }
      .checklog{
        width: 70%;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        font-size: 0.6em;
        align-items: center;
        .check{
          width: 20%;
          display: flex;
          justify-content: center;
          align-items: center;
          span{
           margin-top: 7px;
          }
        }
        a{
          span{
            color: #ffffff;
          }
        }
      }
      a{
        text-decoration: none;
        input[type=submit]{
          width: 100px;
          margin: 0 auto;
          padding: 5px 10px;
          background-color: #ffffff;
          color:#1A417c ;
          
          &:hover{
              background-color: #1A417c;
              color: #ffffff;
              border: 1px solid #ffffff;
              text-decoration: none;
          }
        }
      }
      
    }
  }


  .signup{
    background-color: #1A417c;
    color: #ffffff;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    .signup-container{
      width: 50%;
      text-align: center;
      input{
        display: block;
        padding: 7px 10px;
        width: 70%;
        background-color: #1A417c;
        color: #ffffff;
        border: 1px solid #ffffff;
        margin: 1em auto;
        margin-bottom: 0;
       
      }
      .checklog{
        width: 70%;
        font-size: 0.8em;
        display: flex;
        justify-content: center;
        margin: 0 auto;
       border: 1px solid red;
        .input{
          width: 10%;
          .span{
            width: 80%;
            border: 2px solid #ffffff;
          }
        }
      }
      input[type=submit]{
        width: 100px;
        margin: 0 auto;
        padding: 5px 10px;
        background-color: #ffffff;
        color:#1A417c ;
        text-decoration: none;
        &:hover{
            background-color: #1A417c;
            color: #ffffff;
            border: 1px solid #ffffff;
        }
      }
    }
  }
}